*, :before, :after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  font-family: inherit;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  display: none;
  position: absolute;
  overflow: hidden;
}

.loader {
  width: 96px;
  box-sizing: content-box;
  height: 48px;
  background: #fff;
  border: 2px solid #de3500;
  border-bottom-width: 50px;
  border-radius: 100%;
  animation: yinYang 3s linear infinite;
  position: relative;
}

.loader:before {
  content: "";
  width: 12px;
  height: 12px;
  box-sizing: content-box;
  background: #fff;
  border: 18px solid #de3500;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 0;
}

.loader:after {
  content: "";
  width: 12px;
  height: 12px;
  box-sizing: content-box;
  background: #de3500;
  border: 18px solid #fff;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes yinYang {
  100% {
    transform: rotate(360deg);
  }
}

.container {
  margin: 15px auto;
  padding: 0 20px;
}

.cat-info {
  margin: 10px auto;
}

.ss-main {
  width: 20%;
  max-width: 230px;
  min-width: 170px;
}

.cat-info-container {
  max-width: 375px;
  background-color: #fff;
  border-radius: 15px;
  padding: 10px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.cat-info-thumb {
  height: 300px;
  width: 300px;
}

.cat-info-img {
  object-fit: contain;
  object-position: bottom;
}

.cat-info-temp-span {
  font-weight: 600;
}

@media screen and (min-width: 768px) {
  .cat-info {
    justify-content: flex-start;
    align-items: center;
    gap: 18px;
    display: flex;
  }

  .cat-info-thumb {
    height: 350px;
    width: 450px;
  }

  .cat-info-img {
    object-position: center;
  }

  .cat-info-container {
    max-width: 400px;
  }
}

@media screen and (min-width: 1200px) {
  .cat-info-container {
    max-width: 700px;
  }
}



